import React, { useState, useEffect, useCallback } from 'react';
import '../styles/Navbar.css';

const Navbar: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [activeSection, setActiveSection] = useState('home');
  const [scrolled, setScrolled] = useState(false);

  // Improved scroll handler with better section detection
  const handleScroll = useCallback(() => {
    // Update navbar style on scroll
    if (window.scrollY > 50) {
      setScrolled(true);
    } else {
      setScrolled(false);
    }
    
    // Enhanced section detection with better calculations
    const sections = document.querySelectorAll('section[id]');
    const scrollPosition = window.scrollY + 150; // Increased offset for better detection
    
    // Find the section that is currently in view
    let currentSection = '';
    
    sections.forEach((section) => {
      const sectionId = section.getAttribute('id');
      if (!sectionId) return;
      
      const sectionTop = (section as HTMLElement).offsetTop;
      const sectionHeight = section.clientHeight;
      
      // More precise calculation to determine active section
      if (scrollPosition >= sectionTop && scrollPosition < sectionTop + sectionHeight) {
        currentSection = sectionId;
      }
    });
    
    // Only update if we have a valid section and it's different from current active section
    if (currentSection && currentSection !== activeSection) {
      setActiveSection(currentSection);
      
      // Update URL hash without scrolling
      window.history.replaceState(null, '', `#${currentSection}`);
    }
  }, [activeSection]);

  // Improved smooth scroll function with better offset calculation
  const scrollToSection = useCallback((e: React.MouseEvent<HTMLAnchorElement>, sectionId: string) => {
    e.preventDefault();
    
    // Find the target section
    const section = document.getElementById(sectionId);
    
    if (section) {
      // Get the navbar height for offset calculation
      const navbar = document.querySelector('.navbar') as HTMLElement;
      const navbarHeight = navbar ? navbar.offsetHeight : 80;
      
      // Calculate position with better offset
      const sectionTop = section.offsetTop;
      const offset = navbarHeight + 20; // Increased padding
      
      // Scroll with smooth behavior
      window.scrollTo({
        top: sectionTop - offset,
        behavior: 'smooth'
      });
      
      // Update active section
      setActiveSection(sectionId);
      
      // Update URL without triggering another scroll
      window.history.pushState(null, '', `#${sectionId}`);
      
      // Close mobile menu if open
      setIsMenuOpen(false);
      
      // Add highlight effect to the section
      section.classList.add('section-highlight');
      setTimeout(() => {
        section.classList.remove('section-highlight');
      }, 1500);
    }
  }, []);

  // Enhanced toggle for menu with animation timing
  const toggleMenu = useCallback(() => {
    setIsMenuOpen(prev => !prev);
    
    // Add body class to prevent scrolling when menu is open on mobile
    if (!isMenuOpen) {
      document.body.classList.add('menu-open');
    } else {
      document.body.classList.remove('menu-open');
    }
  }, [isMenuOpen]);

  // Handle click outside to close menu
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const menuElement = document.querySelector('.menu-items');
      const menuButton = document.querySelector('.menu-icon');
      
      if (
        isMenuOpen &&
        menuElement && 
        menuButton &&
        !(menuElement as HTMLElement).contains(event.target as Node) &&
        !(menuButton as HTMLElement).contains(event.target as Node)
      ) {
        setIsMenuOpen(false);
        document.body.classList.remove('menu-open');
      }
    };
    
    document.addEventListener('mousedown', handleClickOutside);
    
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isMenuOpen]);

  // Handle initial section detection and URL hash
  useEffect(() => {
    // Add scroll and resize event listeners
    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', handleScroll);
    
    // Check for hash in URL on load and scroll to that section
    const initialHash = window.location.hash.substring(1);
    if (initialHash) {
      const section = document.getElementById(initialHash);
      if (section) {
        // Delay to ensure DOM is fully loaded
        setTimeout(() => {
          const navbar = document.querySelector('.navbar') as HTMLElement;
          const navbarHeight = navbar ? navbar.offsetHeight : 80;
          
          window.scrollTo({
            top: section.offsetTop - navbarHeight - 20,
            behavior: 'smooth'
          });
          
          setActiveSection(initialHash);
        }, 500);
      }
    }
    
    // Initial scroll check after DOM is ready
    setTimeout(handleScroll, 300);
    
    // Cleanup
    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleScroll);
      document.body.classList.remove('menu-open');
    };
  }, [handleScroll]);

  // Menu items array for easier management
  const menuItems = [
    { id: 'home', label: 'Home' },
    { id: 'about', label: 'About' },
    { id: 'skills', label: 'Skills' },
    { id: 'works', label: 'Works' },
    { id: 'contact', label: 'Contact' }
  ];

  return (
    <nav className={`navbar ${scrolled ? 'scrolled' : ''}`}>
      <div className="navbar-container">
        <a 
          href="#home" 
          onClick={(e) => scrollToSection(e, 'home')} 
          className="navbar-logo"
        >
          <span className="logo-text">Marlon</span>
          <span className="logo-dot"></span>
        </a>
        
        <div className="menu-icon" onClick={toggleMenu}>
          <div className={`hamburger ${isMenuOpen ? 'active' : ''}`}>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
        
        <ul className={`menu-items ${isMenuOpen ? 'active' : ''}`}>
          {menuItems.map(item => (
            <li key={item.id}>
              <a 
                href={`#${item.id}`} 
                className={activeSection === item.id ? 'active' : ''}
                onClick={(e) => scrollToSection(e, item.id)}
              >
                {item.label}
                {activeSection === item.id && <span className="active-indicator"></span>}
              </a>
            </li>
          ))}
        </ul>
      </div>
    </nav>
  );
};

export default Navbar; 