/**
 * Telegram Bot Service
 * This service handles sending form data to a Telegram bot
 */

// Replace these values with your own
const TELEGRAM_BOT_TOKEN = '7831594508:AAFVmrW_A7aKxMtPhVDB6umxdogtO-VMoVk';
const TELEGRAM_CHAT_ID = '6250306990';

// Rate limiting configuration
const RATE_LIMIT = {
  maxRequests: 5,  // Maximum requests allowed
  timeWindow: 3600000, // Time window in milliseconds (1 hour)
  ipMap: new Map<string, { count: number, timestamp: number }>()
};

interface ContactFormData {
  name: string;
  email: string;
  message: string;
}

/**
 * Check if the request is within rate limits
 * @param ip The IP address of the requester
 * @returns Boolean indicating if the request is allowed
 */
const checkRateLimit = (ip: string): boolean => {
  const now = Date.now();
  const ipData = RATE_LIMIT.ipMap.get(ip);
  
  if (!ipData) {
    // First request from this IP
    RATE_LIMIT.ipMap.set(ip, { count: 1, timestamp: now });
    return true;
  }
  
  // Check if the time window has passed
  if (now - ipData.timestamp > RATE_LIMIT.timeWindow) {
    // Reset counter for new time window
    RATE_LIMIT.ipMap.set(ip, { count: 1, timestamp: now });
    return true;
  }
  
  // Check if max requests exceeded
  if (ipData.count >= RATE_LIMIT.maxRequests) {
    return false;
  }
  
  // Increment request count
  RATE_LIMIT.ipMap.set(ip, { count: ipData.count + 1, timestamp: ipData.timestamp });
  return true;
};

/**
 * Sends form data to a Telegram bot
 * @param formData The form data to send
 * @param ip The IP address of the requester (optional)
 * @returns Promise with the response
 */
export const sendToTelegram = async (
  formData: ContactFormData, 
  ip: string = 'unknown'
): Promise<{ success: boolean; message: string }> => {
  try {
    // Check rate limit if IP is provided
    if (ip !== 'unknown' && !checkRateLimit(ip)) {
      return {
        success: false,
        message: 'Too many requests. Please try again later.',
      };
    }
    
    // Format the message
    const message = `
🔔 *New Contact Request!*

👤 *Name:* ${formData.name}
📧 *Email:* ${formData.email}
📝 *Message:* 
${formData.message}

⏰ *Time:* ${new Date().toLocaleString()}
🌐 *IP:* ${ip}
`;

    // Send the message to Telegram
    const response = await fetch(`https://api.telegram.org/bot${TELEGRAM_BOT_TOKEN}/sendMessage`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        chat_id: TELEGRAM_CHAT_ID,
        text: message,
        parse_mode: 'Markdown',
      }),
    });

    const data = await response.json();

    if (!response.ok) {
      throw new Error(data.description || 'Error sending message to Telegram');
    }

    return {
      success: true,
      message: 'Message sent successfully!',
    };
  } catch (error) {
    console.error('Error sending message to Telegram:', error);
    return {
      success: false,
      message: error instanceof Error ? error.message : 'An unknown error occurred',
    };
  }
};

/**
 * Validates Telegram bot settings
 * @returns Promise with validation status
 */
export const validateTelegramSettings = async (): Promise<boolean> => {
  if (!TELEGRAM_BOT_TOKEN || TELEGRAM_BOT_TOKEN === '7831594508:AAFVmrW_A7aKxMtPhVDB6umxdogtO-VMoVk') {
    console.error('Telegram bot token is not set. Please update TELEGRAM_BOT_TOKEN in telegramService.ts');
    return false;
  }

  if (!TELEGRAM_CHAT_ID || TELEGRAM_CHAT_ID === '6250306990') {
    console.error('Chat ID is not set. Please update TELEGRAM_CHAT_ID in telegramService.ts');
    return false;
  }

  return true;
}; 
